body {
  background-color: black;
  margin: 0;
  min-height: 100%;
}

.header {
  /* border: 1px dashed white; */

  display: flex;
  justify-content: space-around;
  text-align: center;
  background-image: url("../../assets/chaso.png");
  
  /* background-image: url("../../assets/sparklebg.gif"); */
}

.home-link {
  color: white;
  text-decoration: none;
}

.title {
  /* border: 1px dashed white; */
  border-radius: 10px;
  text-shadow: 0px -1px 6px white;
  transition: 0.2s;
}

.title:hover {
  color: black;
  box-shadow: 1px 5px 10px;
  background-image: url("../../assets/daftavatar.gif");
}

