.footer {
    border: 1px dashed black;
    background-image: url("../../assets/background-2.gif");
    justify-content: center;
    text-align: center;
    position:relative;
}

#link-footer {
    color: darkred;
}

.footerbar {
    display: flex;
    justify-content: space-around;
    padding-left: 500px;
    padding-right: 500px;
}

.footer-sc:hover {
    background-image: url('../../assets/cd6.gif');
    text-shadow: 1px 1px 1px black;;
}
  
.footer-ig:hover {
    background-image: url('../../assets/Fire_burns.gif');
    text-shadow: 1px 1px 1px black;;
}
  
.footer-yt:hover {
    background-image: url('../../assets/twirl.gif');
    text-shadow: 1px 1px 1px black;;
}

.outro {
    /* border: 1px dashed white; */

    color: darkred;
}

@media screen and (max-width: 1258px) {
    .footerbar {
        padding-left: 100px;
        padding-right: 100px;
    }
}

@media screen and (max-width: 768px) {
    .footerbar {
        padding: 0;
    }
}